.vertical_txt {

    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    text-transform: uppercase;
    font-weight: 600;
    text-align: center;


}

.c-btns {
    display: flex;
    justify-content: center;
    margin-top: 100px;

}

.c-btns__btn {
    padding: 20px 25px;
    width: 455px;
    height: 257px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    border: 4px solid $black;
    cursor: pointer;
    font-size: 32px;
    line-height: 1.3;
    text-transform: uppercase;
    transition: all .3s;
    margin: 9px;

    &:hover {
        border: 4px solid #BA205A;
    }



    span {
        height: 100%;
        display: flex;
        align-items: center
    }
}

.c-btns__txt {
    margin-bottom: 30px;
}

@media only screen and (max-width : 1600px) {
    .c-btns {
        margin-top: 0px;
        flex-wrap: wrap;

    }

    .c-btns__btn {
        padding: 20px 40px;
        width: 455px;
        height: 257px;

    }
}

@media only screen and (max-width : 1200px) {
    .active {
        .vertical_txt {
            color: $black;
        }
    }

    .vertical_txt {
        writing-mode: horizontal-tb;
        transform: none;
        text-transform: uppercase;
        font-weight: 600;
    }

    .step-calc {
        width: 50%;
    }
}

@media only screen and (max-width : 894px) {
    .vertical_txt {
        font-size: 26px;

    }

    .c-btns__btn {
        margin: 0;
        margin-bottom: 10px;
    }

}

@media only screen and (max-width : 872px) {
    .c-btns {

        margin-top: 50px;

    }

    .c-btns__btn {
        padding: 24px 30px;
        width: 405px;
        height: 207px;
        font-size: 24px;


    }


}



@media only screen and (max-width : 586px) {
    .vertical_txt {
        font-size: 22px;
        line-height: 1.3;
    }

    .c-btns {
        margin-top: 10px;
    }

    .c-btns__btn {
        padding: 20px 30px;
        width: 100%;
        height: 150px;
        font-size: 18px;

    }

    .c-btns__txt {
        margin-bottom: 17px;
    }


}
